body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #a39685;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {

  width: 100%;
  height: 100%;

  background-color: #a39685;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
}
.node {
    width: 25px;
    height: 25px;
    outline: 2px solid rgb(143, 132, 119);
    display: inline-block;

    overflow: visible;

    margin-left: 4px;

    position: relative;
}

.node-destination {
    background-color: rgb(173, 97, 53) !important;
}

.node-visited {

    -webkit-animation-name: visitedAnimation;

            animation-name: visitedAnimation;
    -webkit-animation-duration: 0.2s;
            animation-duration: 0.2s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
}

@-webkit-keyframes visitedAnimation {
    0% {
      -webkit-transform: scale(0.3);
              transform: scale(0.3);
      background-color: rgb(97, 81, 62);
      border-radius: 100%;
    }
  
    50% {
      background-color: rgb(151, 134, 113);
    }
  
    75% {
      background-color: rgb(187, 173, 156);
    }
  
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      background-color: rgb(206, 192, 175);
    }
  }

@keyframes visitedAnimation {
    0% {
      -webkit-transform: scale(0.3);
              transform: scale(0.3);
      background-color: rgb(97, 81, 62);
      border-radius: 100%;
    }
  
    50% {
      background-color: rgb(151, 134, 113);
    }
  
    75% {
      background-color: rgb(187, 173, 156);
    }
  
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      background-color: rgb(206, 192, 175);
    }
  }
  
  .node-wall {
    background-color: rgb(12, 53, 71);
  }
  
  .node-shortest-path {
    -webkit-animation-name: shortestPath;
            animation-name: shortestPath;
    -webkit-animation-duration: 0.1s;
            animation-duration: 0.1s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
  }
  
  @-webkit-keyframes shortestPath {
    0% {
      -webkit-transform: scale(0.6);
              transform: scale(0.6);
      background-color: rgb(17, 87, 0);
    }
  
    50% {

      background-color: rgb(214, 255, 161);
    }
  
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      background-color: rgb(150, 255, 109);
    }
  }
  
  @keyframes shortestPath {
    0% {
      -webkit-transform: scale(0.6);
              transform: scale(0.6);
      background-color: rgb(17, 87, 0);
    }
  
    50% {

      background-color: rgb(214, 255, 161);
    }
  
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      background-color: rgb(150, 255, 109);
    }
  }

  .node-distance {

    font-size: 12px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: rgb(255, 255, 255);
    z-index: 1;
  }
.grid {

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#destination-node-display {

    width: 200px;
    height: 150px;

    border: 10px solid #928677;

    margin: 20px;

    text-align: center;

    border-radius: 28px;
    background: rgb(190, 177, 156);
    box-shadow:  6px 6px 12px #8b8071,
             -6px -6px 12px #bbad99;

    color: #6d5f4e;

    transition: 0.3s;
}

#destination-node-display:hover {

    -webkit-transform: scale(1.1);

            transform: scale(1.1);
}

#buttons {

    margin: 30px;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    grid-gap: 50px;

    gap: 50px;
}

#directions {

    width: 350px;
    height: 170px;

    margin: 20px;

    text-align: center;

    border-radius: 40px;

    color: #6d5f4e;

    border: 10px solid #928677;

    border-radius: 28px;
    background: rgb(190, 177, 156);
    box-shadow:  6px 6px 12px #8b8071,
             -6px -6px 12px #bbad99;

    transition: 0.3s;
}

#directions:hover {

    -webkit-transform: scale(1.1);

            transform: scale(1.1);
}

#header {

    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    grid-gap: 100px;

    gap: 100px;

    margin: 10px;

}
